@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

svg {
  cursor: pointer;
}

button {
  background-color: #141414;
  border: 1px solid rgba(54, 54, 54, 0.6);
  border-radius: 11px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 45px;
  color: white;
  width: 130px;
  opacity: 1;
  font-family: 'Poppins';
font-style: normal;

}

a {
  text-decoration: none;
  color: white;
}
.mainContainer {
  display: flex;
  gap: 20px;
  margin-inline: 20px;
  margin-top: 30px;

  height: 69vh;
}

.loadingIconContainer {
  position: absolute;
  z-index: 1;
  width: 200px;
  height: 100px;
  left: 40%;
  top: 55%;
}

.graphContainer {
  width: 75%;
}

.graphActions {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 25px;
}

.graphActions svg {

  border: 1px solid #E4E7EC;
  border-radius: 6px;
  background: #ffff;
  padding: 8px 11.5px ;
}
.wrapper {
  background-color: #ffff;
  height: 100vh;
}
